.burger-menu-input {
  display: none;
}

.burger-menu-label {
  position: relative;
  width: 35px;
  height: 23px;
  display: inline-block;
  cursor: pointer;
  transition: all 0.5s;
  text-align: left;
}

.burger-menu-line,
.burger-menu-line:before,
.burger-menu-line:after {
  background: #525252;
  position: absolute;
  height: 3px;
  width: 35px;
  border-radius: 4px;
  transition: all 0.5s;
}

.burger-menu-label div:first-child {
  top: 0px;
}
.burger-menu-label div:nth-child(2) {
  top: 10px;
}
.burger-menu-label div:nth-child(3) {
  top: 20px;
}

#burger + label div:first-child {
  transition: top 0.15s 0.15s, transform 0.15s;
}
#burger + label div:nth-child(2),
#burger + label div:nth-child(3) {
  transition: top 0.15s 0.15s, transform 0.15s;
}

#burger:checked + label div:first-child {
  top: 10px;
  transform: rotate(45deg);
  transition: top 0.15s, transform 0.15s 0.15s;
}
#burger:checked + label div:nth-child(2),
#burger:checked + label div:nth-child(3) {
  top: 10px;
  transform: rotate(-45deg);
  transition: top 0.15s, transform 0.15s 0.15s;
}

